            createNewProject() {
                var me = this
                if (me.deleteConfirmCheckBox) {
                    localStorage.removeItem('localLoadData')
                    localStorage.removeItem('localSaveImg')
                    localStorage.removeItem('localSaveProjectName')
                    me.deleteConfirmCheckBox = false
                    //author, projectType, projectId, projectVersion, projectRead,projectWrite
                    me.$router.push({path: `${me.toUrl(me.loginAuthor, 'local', 'init', '-init')}`});
                } else {
