                if (!(JSON.parse(localStorage.getItem('localLoadData')) == null)) {
                    if (JSON.parse(localStorage.getItem('localLoadData'))['definition'].length == 0) {
                        // No data
                        //author, projectType, projectId, projectVersion, projectRead,projectWrite
                        me.$router.push({path: `${me.toUrl(me.loginAuthor, 'local', 'init', '-init')}`});
                    } else {
