                var userId = localStorage.getItem("uid")
                var filter

                return new Promise(function (resolve) {
                    filter = []
                    me.stormingMineKeyList = []
                    me.stormingMineValueList = []

                    firebase.database()
                        .ref('/userLists/' + userId + '/mine/')
                        .once('value', function (snapshot) {
                            if (snapshot.exists()) {
                                var projectIds = Object.keys(snapshot.val())
                                projectIds.forEach(function (projectId) {
                                    if (me.stormingMineKeyList.indexOf(projectId) == -1) {
                                        me.stormingMineKeyList.push(projectId)
                                    }
                                })
                            }

                        }).then(function () {
                        var copystormingMineKeyList = me.stormingMineKeyList
                        me.stormingMineValueList = []

                        for (var idx in copystormingMineKeyList) {

                            firebase.database()
                                .ref('definitions/' + copystormingMineKeyList[idx])
                                .once('value', function (snapshot) {
                                    if (snapshot.exists()) {
                                        var projectId = snapshot.key
                                        var projectAuthor = snapshot.val().shareOptions.author
                                        var projectAuthorEmail = snapshot.val().shareOptions.authorEmail
                                        var projectShareCount = 0
                                        var lastVersion = Object.keys(snapshot.val().versionLists)[Object.keys(snapshot.val().versionLists).length - 1]
                                        var lastVersionInfo = snapshot.val().versionLists[Object.keys(snapshot.val().versionLists)[Object.keys(snapshot.val().versionLists).length - 1]].versionInfo

                                        var permissionWrite = false
                                        if (projectAuthor == userId) {
                                            permissionWrite = true
                                        }

                                        if (snapshot.val().shareOptions.permissions != undefined) {
                                            if(Object.keys(snapshot.val().shareOptions.permissions)=='everyone'){
                                                projectShareCount = -2
                                            }else{
                                                projectShareCount = Object.keys(snapshot.val().shareOptions.permissions).length
                                            }

                                        } else {
                                            projectShareCount = 0
                                        }

                                        var obj = {
                                            projectId: projectId,
                                            projectAuthor: projectAuthor,
                                            projectAuthorEmail: projectAuthorEmail,
                                            saveUser: lastVersionInfo.saveUser,
                                            saveUserName: lastVersionInfo.saveUserName,
                                            projectType: 'mine',
                                            projectVersion: lastVersion,
                                            comment: lastVersionInfo.comment,
                                            date: lastVersionInfo.date,
                                            saveUserEmail: lastVersionInfo.saveUserEmail,
                                            img: lastVersionInfo.img,
                                            projectName: lastVersionInfo.projectName,
                                            write: permissionWrite,
                                            projectShareCount: projectShareCount,
                                        }
                                        // console.log(obj)
                                        filter.push(obj)
                                    } else {
                                        filter = []
                                    }

                                })
                        }
                        me.stormingMineValueList = filter
                        resolve()
                    });
                })
            },
            getShareList() {
                var me = this
                var userId = localStorage.getItem("uid")
                var filter

                return new Promise(function (resolve) {
                    filter = []
                    me.stormingShareKeyList = []
                    me.stormingShareValueList = []

                    firebase.database()
                        .ref('/userLists/' + userId + '/share/')
                        .once('value', function (snapshot) {
                            if (snapshot.exists()) {
                                var projectIds = Object.keys(snapshot.val())
                                projectIds.forEach(function (projectId) {
                                    if (me.stormingShareKeyList.indexOf(projectId) == -1) {
                                        me.stormingShareKeyList.push(projectId)
                                    }
                                })
                            }

                        }).then(function () {
                        var copystormingShareKeyList = me.stormingShareKeyList
                        me.stormingShareValueList = []

                        for (var idx in copystormingShareKeyList) {

                            firebase.database()
                                .ref('definitions/' + copystormingShareKeyList[idx])
                                .once('value', function (snapshot) {
                                    if (snapshot.exists()) {
                                        var projectId = snapshot.key
                                        var projectAuthor = snapshot.val().shareOptions.author
                                        var projectAuthorEmail = snapshot.val().shareOptions.authorEmail

                                        var lastVersion = Object.keys(snapshot.val().versionLists)[Object.keys(snapshot.val().versionLists).length - 1]
                                        var lastVersionInfo = snapshot.val().versionLists[Object.keys(snapshot.val().versionLists)[Object.keys(snapshot.val().versionLists).length - 1]].versionInfo


                                        var permissionWrite = false
                                        if (projectAuthor == userId) {
                                            permissionWrite = true
                                        } else {
                                            if (snapshot.val().shareOptions.permission != undefined) {
                                                permissionWrite = snapshot.val().shareOptions.permission[userId].write
                                            }

                                        }

                                        var obj = {
                                            projectId: projectId,
                                            projectAuthor: projectAuthor,
                                            projectAuthorEmail: projectAuthorEmail,
                                            saveUser: lastVersionInfo.saveUser,
                                            saveUserName: lastVersionInfo.saveUserName,
                                            projectType: 'share',
                                            projectVersion: lastVersion,
                                            comment: lastVersionInfo.comment,
                                            date: lastVersionInfo.date,
                                            saveUserEmail: lastVersionInfo.saveUserEmail,
                                            img: lastVersionInfo.img,
                                            projectName: lastVersionInfo.projectName,
                                            write: permissionWrite,
                                        }
                                        // console.log(obj)
                                        filter.push(obj)
                                    } else {
                                        filter = []
                                    }

                                })
                        }

                        me.stormingShareValueList = filter
                        resolve()
                    });
                })
            },

            getEveryOneList() {
                var me = this
                var userId = localStorage.getItem("uid")
                var filter

                return new Promise(function (resolve) {
                    filter = []
                    me.stormingEveryKeyList = []
                    me.stormingEveryValueList = []

                    firebase.database()
                        .ref('/userLists/everyone/share/')
                        .once('value', function (snapshot) {
                            if (snapshot.exists()) {
                                var projectIds = Object.keys(snapshot.val())
                                projectIds.forEach(function (projectId) {
                                    if (me.stormingEveryKeyList.indexOf(projectId) == -1) {
                                        me.stormingEveryKeyList.push(projectId)
                                    }
                                })
                            }

                        }).then(function () {
                        var copystormingShareKeyList = me.stormingEveryKeyList
                        me.stormingEveryValueList = []

                        for (var idx in copystormingShareKeyList) {

                            firebase.database()
                                .ref('definitions/' + copystormingShareKeyList[idx])
                                .once('value', function (snapshot) {
                                    if (snapshot.exists()) {
                                        var projectId = snapshot.key
                                        var projectAuthor = snapshot.val().shareOptions.author
                                        var projectAuthorEmail = snapshot.val().shareOptions.authorEmail

                                        var lastVersion = Object.keys(snapshot.val().versionLists)[Object.keys(snapshot.val().versionLists).length - 1]
                                        var lastVersionInfo = snapshot.val().versionLists[Object.keys(snapshot.val().versionLists)[Object.keys(snapshot.val().versionLists).length - 1]].versionInfo


                                        var permissionWrite = false
                                        if (projectAuthor == userId) {
                                            permissionWrite = true
                                        }

                                        var obj = {
                                            projectId: projectId,
                                            projectAuthor: projectAuthor,
                                            projectAuthorEmail: projectAuthorEmail,
                                            saveUser: lastVersionInfo.saveUser,
                                            saveUserName: lastVersionInfo.saveUserName,
                                            projectType: 'every',
                                            projectVersion: lastVersion,
                                            comment: lastVersionInfo.comment,
                                            date: lastVersionInfo.date,
                                            saveUserEmail: lastVersionInfo.saveUserEmail,
                                            img: lastVersionInfo.img,
                                            projectName: lastVersionInfo.projectName,
                                            write: permissionWrite,
                                        }
                                        // console.log(obj)
                                        if (projectAuthor != userId) {
                                            filter.push(obj)
                                        }
                                    } else {
                                        filter = []
                                    }

                                })
                        }

                        me.stormingEveryValueList = filter
                        resolve()
                    });

                })
            },
            getList() {
                var me = this

                return new Promise(function (resolve) {

                    var userId = localStorage.getItem("uid")
                    var local = [
                        {
                            // version: '0',
                            // name: localStorage.getItem("localSaveProjectName") != null ? localStorage.getItem("localSaveProjectName") : 'local',
                            author: localStorage.getItem("email") != null ? localStorage.getItem("email") : "anonymous@sample.org",
                            date: localStorage.getItem("localSaveTime") != null ? localStorage.getItem("localSaveTime") : "undefined",
                            img: localStorage.getItem('localSaveImg') != null ? localStorage.getItem('localSaveImg') : 'https://github.com/sooheon45/eventstorming/blob/master/docs/media/evtstrm.jpg?raw=true',
                            userUid: userId != null ? userId : "anonymous",
                            projectId: 'init',
                            projectVersion: '-init',
                            projectName: localStorage.getItem("localSaveProjectName") != null ? localStorage.getItem("localSaveProjectName") : 'local',
                            projectType: 'local',
                        },

                    ]

                    if (userId) {
                        me.stormingLocalList = local

                        me.getMineList().then(function () {
                            resolve()
                        })
                        me.getShareList().then(function () {
                            resolve()
                        })
                        me.getEveryOneList().then(function () {
                            resolve()
                        })
                    } else {
                        me.stormingLocalList = local

                        me.getEveryOneList().then(function () {
                            resolve()
                        })

                        resolve()
                    }

                })


            }
        }
    }
</script>

